.contact{
    padding-left: 140px;
    margin-top: 60px ;
    margin-bottom: 60px;
  
}
.contact hr {
  color : rgb(5, 161, 135);
  width: 400px;
  margin: 10px auto;
}

.contact h2 {
  color : rgb(5, 161, 135);
  font-weight: bold;
}

  .card0 {
    box-shadow: 2px 6px 10px 2px;
    border-radius: 0px;
    margin-top: 25px;
  }
  
  .card2 {
    margin: 0px 40px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .card1 .image {
    padding: 30px;
    margin-top: 30px;
    height: 100%;
    width: 100%;
  }
  .border-line {
    border-right: 1px solid #e0e0e0;
  }
  .line {
    height: 1px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
  }
  
  .or {
    display: flex;
    width: 10%;
    font-weight: bold;
  }
  
  ::placeholder {
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
  }
  
  input,
  textarea {
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 2px;
    margin-top: 5px;
    margin-top: 2px;
    width: 100%;
    color: #138781;
    font-size: 1rem;
    letter-spacing: 1px;
  }
  
  input:focus,
  textarea:focus {
    box-shadow: none !important;
    outline: 1px solid #138781 !important;
    outline-width: 0px;
  }
  
  button:focus {
    box-shadow: none;
    outline-width: 0px;
  }
  
  .button {
    background-color: #1e1e2c;
    color: white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
  }
  
  .button:hover {
    background-color: #000;
    color: white;
  }
  
  /* ======================== */
  @media (max-width: 600px) {
    .contact {
      margin: 0;
      padding: 0;
      margin-top: 50px;
    }
    .contact .card2 {
      margin: 0;
    }
  
    .contact .card2 h6 {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  @media (max-width:600px){
    .contact{
      padding: 0;
      margin: 0;
      margin-top: 50px;
    }

    .contact .hr{
      width: 280px;
    }
  }